$full-width: 100%;
$gutter: 1.5%;
$halfGutter: 0.5%;
// Lastly: use the scw to calculate the rest of the column widths using the following formula:
// cw = (scw * cs) + (m * (cs – 1))
// Where:
//   cw = column width
//   scw = single column width (6.86666666667%)
//   cs = column span (1-12)
//   m = margin (1.6%)
.patron-container {
    width: 100%;
}

.row {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: $halfGutter;
}

.bgColor {
    background: #eee;
}

.col-1 {
    flex-basis: $full-width;
    padding: 0 $halfGutter;
    @include tablet {
        flex-basis: grid-width(1);
        max-width: grid-width(1);
    }
    @include desktop {
        flex-basis: grid-width(1);
        max-width: grid-width(1);
    }
}

.col-2 {
    flex-basis: $full-width;
    padding: 0 $halfGutter;
    @include tablet {
        flex-basis: colums-width(2);
        max-width: colums-width(2);
    }
    @include desktop {
        flex-basis: colums-width(2);
        max-width: colums-width(2);
    }
}

