.margin {
    margin: $base-margin;
}

// Margin and Padding
@for $i from 0 to 10 {
    .mt-#{$i} {
        margin-top: $i * 4px !important;
    }
    .mb-#{$i} {
        margin-bottom: $i * 4px !important;
    }
    .ml-#{$i} {
        margin-left: $i * 4px !important;
    }
    .mr-#{$i} {
        margin-right: $i * 4px !important;
    }
    .margin-around-#{$i} {
        margin: $i * 4px !important;
    }
    .pt-#{$i} {
        margin-top: $i * 4px !important;
    }
    .pb-#{$i} {
        margin-bottom: $i * 4px !important;
    }
    .pl-#{$i} {
        margin-left: $i * 4px !important;
    }
    .pr-#{$i} {
        margin-right: $i * 4px !important;
    }
    .padding-around-#{$i} {
        margin: $i * 4px !important;
    }
}